

























































ul {
  list-style: none;
}
.inhabitant__info {
  width:90%;
  margin: 0 auto;
}

.inhabitant__image {
  max-width: 250px;
  height: 250px;
  margin: 0 auto;
  img {
    width: 100%;
    height: 100%;
    border-radius: 50%;
  }
}

.detail__button {
  padding: 20px;
  text-align: center;
}


@media (min-width: 768px) {
.inhabitant__info {
  width:300px;
  margin: 0 auto;
  padding: 20px;
}
}
